@import url("https://fonts.googleapis.com/css2?family=Barlow&family=Inter+Tight&family=Work+Sans&display=swap");

* {
  text-align: center;
  justify-content: center;
  font-family: "Inter Tight", sans-serif;
  box-sizing: border-box;
}

.main-title {
  color: rgb(0, 150, 255);
  font-size: 3rem;
}

// Style for To Do List Items
.task-list {
  padding-right: 5vh;
  list-style-type: none;
  cursor: pointer;
  margin: 0 35vw;
}

.task-icons {
  padding: 0 5px;
  font-size: 20px;
}

.list-items {
  display: flex;
  border: 2px rgb(0, 150, 255) solid;
  border-radius: 5px;
  margin: 15px 0;
  align-items: center;
  box-shadow: 3px 3px rgb(0, 150, 255);
  li {
    margin: 0px 15px;
    font-family: "Barlow", sans-serif;
    font-size: 28px;
    height: auto;
    padding-bottom: 0.5vh;
  }
}

.check {
  color: rgb(0, 150, 255);
}

.remove {
  color: red;
}

.priority {
  padding-bottom: 0.3vh;
}

// Style for User Input, Adding Tasks

#input {
  text-align: left;
  height: 5vh;
  border-radius: 5px;
  border: 2px solid rgb(0, 150, 255);
  width: 20vw;
  font-size: 24px;
}

#input-label {
  font-weight: 600;
}

.input-btns {
  display: flex;
  height: 5vh;
}

.add-task {
  background-color: rgb(0, 150, 255);
  color: white;
  border: 1px black solid;
  border-radius: 5px;
  padding: 10px;
  height: 5vh;
}

// Style for Tasks Count
.num-tasks {
  font-family: "Work Sans", sans-serif;
}

.task-number {
  color: rgb(0, 150, 255);
}

// Style for Completed Bar

.completed {
  display: flex;
  p {
    font-family: "Inter Tight", sans-serif;
    font-size: 30px;
  }
}

.completed-bar {
  border: 1px grey solid;
  margin: 0 40vw;
}

.list-arrows {
  padding-top: 3.5vh;
  padding-left: 30px;
  font-size: 28px;
  cursor: pointer;
}

.clear-all {
  padding: 0 20px;
  padding-top: 4.2vh;
}

.complete-list-items {
  display: flex;
  list-style-type: none;
  li {
    text-decoration: line-through;
    margin: 0px 15px;
    font-size: 28px;
    font-family: "Barlow", sans-serif;
  }
}

.undo-task {
  font-size: 24px;
  padding-top: 0.7vh;
  color: rgb(92, 163, 98);
  cursor: pointer;
}

.complete-list {
  display: none;
}

.completed-num {
  color: rgb(92, 163, 98);
}

// Style for Sub Tasks List
.subtask-div {
  display: none;
}

.create-subtask {
  height: 3vh;
  display: flex;
  input {
    height: 3vh;
    font-size: 20px;
  }

  button {
    height: 3vh;
    border-radius: 5px;
    background-color: rgb(0, 150, 255);
    color: white;
    border: 1px black solid;
  }
}

.subtask-list-items {
  list-style-type: none;
  padding-right: 3vw;
}

.subtask-list {
  display: flex;
  border: 2px solid rgb(0, 150, 255);
  border-radius: 5px;
  margin: 10px 0;

  li {
    padding-right: 5px;
  }
}

.subtask-icons {
  padding: 4px 5px;
  font-size: 16px;
}

.subtask-length {
  @extend .task-number;
  font-weight: 600;
}

// 2. Mobile Design

@media screen and (max-width: 450px) {
  .main-title {
    font-size: 2rem;
  }
  .completed-bar {
    margin: 0;
  }
  #input,
  .add-task {
    transform: scale(120%);
  }

  #input {
    width: 52vw;
    font-size: 16px;
    margin-left: 3vw;
  }

  .add-task {
    margin-left: 6.3vw;
  }

  .list-arrows {
    padding-top: 3.3vh;
    font-size: 30px;
  }

  .list-items li,
  .complete-list li {
    font-size: 1.1rem;
  }

  .task-list {
    width: 90vw;
    margin: 0 5vw;
  }

  .task-icons {
    font-size: 18px;
  }

  .completed {
    p {
      font-size: 26px;
    }
  }

  .undo-task {
    font-size: 18px;
    padding-top: 0.5vh;
  }

  .clear-all {
    padding-top: 3.6vh;
  }

  .subtask-list {
    margin-right: 5vw;
  }
}

// Mobile Landscape Design

@media screen and (min-width: 450px) and (max-width: 1024px) {
  .main-title {
    font-size: 42px;
  }

  .input-btns {
    height: 10vh;
  }

  #input {
    width: 50vw;
    height: 10vh;
  }

  .add-task {
    height: 10vh;
  }

  .task-list {
    width: 50vw;
    margin: 0 25vw;
  }

  .subtask-div {
    width: 100vw;
  }

  .create-subtask {
    height: 7vh;
    input {
      height: 7vh;
    }

    button {
      height: 7vh;
    }
  }

  .subtask-list-items {
    width: 35vw;
    margin-left: 2vw;
  }

  .completed-bar {
    width: 50vw;
    margin: 0 25vw;
  }

  .clear-all {
    font-size: 28px;
    padding-top: 8vh;
  }

  .list-arrows {
    font-size: 32px;
    padding-top: 8vh;
  }
}

// 3.Ipad Design

@media screen and (min-width: 1024px) and (max-width: 1200px) {
  body {
    font-size: 1.5rem;
  }

  #input,
  .add-task {
    transform: scale(150%);
  }

  #input {
    width: 39vw;
    margin-left: 6vw;
    font-size: 20px;
    height: 3vh;
  }

  .add-task {
    margin-left: 11.2vw;
    height: 3vh;
    padding: 0.5vh;
  }

  .completed-bar {
    margin: 0;
  }

  .list-items li,
  .complete-list-items li {
    font-size: 1.8rem;
  }

  .completed,
  .completed p {
    font-size: 2rem;
  }

  .list-arrows {
    padding-top: 3.5vh;
    font-size: 48px;
  }

  .undo-task {
    font-size: 32px;
    padding-top: 0.5vh;
  }

  .task-list {
    margin: 0 10vw;
  }

  .task-icons {
    font-size: 26px;
  }

  .list-items {
    margin: 35px 0;
  }
}
